
import { PropType } from "vue";
export default {
  name: "RadioGroup",
  emits: ["onchange"],
  props: {
    fieldLabel: {
      type: String,
      required: true,
    },
    attention: {
      type: String,
      required: false,
    },
    name: {
      type: String,
      required: true,
    },
    items: Array as PropType<
      Array<{
        value: String;
        checked: Boolean;
        writeIn?: Boolean;
      }>
    >,
  },
  setup(_: unknown, { emit }: { emit: any }) {
    const onchange = (e: any) => emit("onchange", e);
    return {
      onchange,
    };
  },
};
